.footer {
  background-color: var(--bg-color);
  padding-top: 17px;
  padding-bottom: 10px;

  @include large-desktop {
    padding-top: 18px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: var(--grid-gap);
    margin-bottom: 17px;
  }

  &__logo {
    grid-column: 3 span;

    @include large-desktop {
      grid-column: 2 span;
    }
  }

  &__right {
    grid-column: 9 span;
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    gap: var(--grid-gap);

    @include big-desktop {
      grid-column: 10 span;
    }
  }

  &__nav {
    grid-column: 2 span;

    &--block {
      grid-column: 3 span;

      @include big-desktop {
        margin-left: auto;
      }
    }
  }

  &__nav-title {
    font-weight: 800;
    font-size: 16px;
    line-height: 16px;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 7px;
  }

  &__nav-link {
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 3px;
    width: 50px;
    height: 30px;
    padding: 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    vertical-align: bottom;
    transition: background-color 0.3s ease-in-out;

    &:hover {
      background-color: var(--light-color);
    }

    &:not(:last-child) {
      margin-right: 5px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: var(--footerLink-color);
    transition: color 0.3s ease-in-out;

    &:hover {
      color: var(--footerLink-color-hover);
    }
  }

  &__nav-img {
    margin-bottom: 25px;
  }

  &__nav-link-social {
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 3px;
    width: 30px;
    height: 30px;
    padding: 5px;
    display: inline-flex;
    justify-content: center;
    transition: background-color 0.3s ease-in-out;

    &:hover {
      background-color: var(--light-color);
    }
  }


  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__artix {
    fill: #FAFEFD;
    width: 20px;
    height: 21px;
    margin-left: 13px;
  }
}
